import logo from './logo.svg';
import './App.css';
import React, { useState, useEffect } from 'react';
import axios from 'axios';

function App() {

  const [songs, setSongs] = useState([]);

  useEffect(() => {
    axios({
      method: "GET",
      url: 'http://127.0.0.1:8000/api/test-api/'
    }).then(response => {
      setSongs(response.data)
    })
    }, [])

  return (
    <div className="App">
      <header className="App-header">
        <img src={logo} className="App-logo" alt="logo" />
        <ul>
          {songs.map(song => (
            <li key={song.id}>{song.country}</li>
          ))} 
        </ul>
        <img src="node_modules/svg-country-flags/svg/no.svg" height="50px" width="50px" title="Norwegian flag" alt='test_flag'/>
        <span class="ip2location-flag-16 flag-au"></span>
        <a
          className="App-link"
          href="https://reactjs.org"
          target="_blank"
          rel="noopener noreferrer"
        >
          Learn React
        </a>
      </header>
    </div>
  );
}

export default App;
